.container {
  margin: 10px auto; }
  @media (min-width: 768px) {
    .container {
      margin: 20px auto; } }
  @media (min-width: 1280px) {
    .container {
      margin: 40px auto; } }
  @media (min-width: 540px) {
    .container {
      max-width: 580px; } }
  @media (min-width: 800px) {
    .container {
      max-width: 860px; } }
  @media (min-width: 1060px) {
    .container {
      max-width: 1180px; } }

.empty-note {
  display: none; }

.tool {
  border-bottom: 1px solid #D8D8D8;
  margin-bottom: 20px; }
  .tool__box, .tool__title, .tool__type, .tool__tag {
    display: inline-block;
    vertical-align: middle; }
  .tool__box {
    margin: 0 20px; }
  .tool__title {
    color: #787878;
    line-height: 1;
    font-size: 14px;
    padding-right: 20px; }
  .tool__type {
    margin: 0 -3px; }
  .tool__tag {
    color: #787878;
    font-size: 12px;
    line-height: 1;
    border-radius: 4px;
    position: relative;
    overflow: hidden;
    margin: 5px 3px; }
    .tool__tag--active {
      color: #FFFFFF;
      background-color: #4283E4; }
    .tool__tag label {
      display: block;
      line-height: 1;
      padding: 6px 8px; }
    .tool__tag input[type=radio] {
      position: absolute;
      left: -1px;
      top: -1px;
      height: 1px;
      width: 1px;
      z-index: -1;
      opacity: 0; }
  @media (max-width: 799px) {
    .tool__title, .tool__type {
      display: table-cell;
      vertical-align: top;
      line-height: 34px; }
    .tool__title {
      white-space: nowrap;
      margin-top: 2px; } }

.video-list {
  width: 100%;
  text-align: justify;
  font-size: 0; }
  .video-list:after {
    content: '';
    width: 100%;
    display: inline-block;
    font-size: 0;
    line-height: 0; }

@media (max-width: 537px) {
  .video-list {
    text-align: center; } }



